































































import { Component, Vue } from "vue-property-decorator";
import { EditAuth } from "../../mixins";
import AddBanner from "./add.vue";
import EditBanner from "./edit.vue";
import { apiUrl } from "../../api";
import Pagination from "../../components/pagination/pagination.vue";
@Component({
    name: "banner",
    components: {
        AddBanner,
        Pagination,
        EditBanner
    }
})
export default class Banner extends EditAuth {
    private dialogFlag = false;
    public page = 1;
    public pageSize = 15;
    public count = 1;
    private tableData = [];
    private multipleSelection = [];
    private editDialogFlag = false;
    private editData = {};
    private showInfo = false;
    private handleSelectionChange(val) {
        this.multipleSelection = val;
    }
    private handleEdit(index, row) {
        console.log(index, row);
        this.editDialogFlag = true;
        this.editData = { ...row };
    }
    editDialogClose() {
        this.editDialogFlag = false;
        this.editData = {};
    }
    private handleDelete(index: number, row: any) {
        console.log(index, row);
        this.$confirm("是否删除此条数据?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        })
            .then(() => {
                this.showInfo = true;
                this.deleteItem(row.id);
            })
            .catch(() => {});
    }
    private deleteItem(id: number) {
        (this as any).$axios
            .get(`${apiUrl.bannerDelete}?id=${id}`)
            .then((res: any) => {
                if (res.data.code === 0) {
                    if (this.showInfo) {
                        this.$message.success("删除成功");
                        this.showInfo = false;
                    }
                }
            });
    }
    created() {
        this.getBannerList();
    }
    addBanner() {
        this.dialogFlag = true;
    }
    closeAddDialog() {
        this.dialogFlag = false;
    }
    getBannerList() {
        (this as any).$axios
            .get(
                `${apiUrl.bannerGet}?page=${this.page}&pageSize=${this.pageSize}`
            )
            .then((res: any) => {
                let datas = res.data;
                if (datas.code == 0) {
                    this.tableData = datas.message.rows;
                    this.count = datas.message.count;
                }
            });
    }
    setPage(page: number) {
        this.page = page;
        this.getBannerList();
    }
    setPageSize(pageSize: number) {
        this.pageSize = pageSize;
        this.getBannerList();
    }
    private changState(id: number, state: boolean) {
        (this as any)
            .$axios(`${apiUrl.bannerState}?id=${id}&banner_state=${state}`)
            .then((res: any) => {
                let datas = res.data;
                if (datas.code === 0) {
                    this.$message.success("修改成功");
                    this.getBannerList();
                }
            });
    }
    private removeAll() {
        this.$confirm("是否批量删除数据?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
        })
            .then(() => {
                let arr = this.multipleSelection;
                if (arr.length <= 0) {
                    this.$message.warning("请选择数据");
                    return false;
                }
                arr.forEach((item: any, index: number) => {
                    if (index >= arr.length - 1) {
                        this.showInfo = true;
                    }
                    this.deleteItem(item.id);
                    this.getBannerList();
                });
            })
            .catch(() => {});
    }
}
